.product-detail {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
}

.button-container {
    display: flex;
    justify-content: center; /* Centra horizontalmente */
    align-items: center; /* Centra verticalmente */
    height: 100px; /* Establece una altura deseada o elimina esto según tus necesidades */
}  

.product-detail img {
    max-width: 100%;
    height: auto;
    margin-bottom: 20px;
}

.product-detail h2 {
    font-size: 1.5rem;
    margin: 10px 0;
}

.product-detail p {
    font-size: 1.2rem;
    margin: 5px 0;
}

.product-detail a {
    display: inline-block;
    margin-top: 10px;
    padding: 10px 20px;
    background-color: #007bff;
    color: #fff;
    text-decoration: none;
    border-radius: 5px;
    transition: background-color 0.3s ease-in-out;
}

.product-detail a:hover {
    background-color: #0056b3;
}