/* Estilos para el título <h1> */
h1 {
    font-size: 2rem;
    /* Tamaño de fuente */
    text-align: center;
    /* Centra el texto horizontalmente */
}

.button-container {
    display: flex;
    justify-content: center; /* Centra horizontalmente */
    align-items: center; /* Centra verticalmente */
    height: 100px; /* Establece una altura deseada o elimina esto según tus necesidades */
}  

.product-list {
    display: grid;
    flex-wrap: wrap;
    gap: 20px;
    grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
    /* Crea columnas que se ajustan automáticamente */
    gap: 20px;
    border-radius: 10px;
    /* Ajusta el valor para controlar la curvatura de las esquinas */
    padding: 20px;
    /* Opcional: agrega espacio interior para que el contenido no toque los bordes redondeados */
    background-color: #f0f0f0;
    /* Opcional: agrega un fondo para resaltar los bordes redondeados */

}

.product {
    border: 1px solid #ccc;
    border-radius: 10px;
    padding: 20px;
    max-width: 300px;
    text-align: center;
    transition: transform 0.3s ease-in-out;
}

.product:hover {
    transform: scale(1.05);
}

.product img {
    max-width: 100%;
    height: auto;
}

.product h2 {
    font-size: 1.2rem;
    margin: 10px 0;
}

.product p {
    font-size: 1rem;
}

.product a {
    display: inline-block;
    margin-top: 10px;
    padding: 10px 20px;
    background-color: #007bff;
    color: #fff;
    text-decoration: none;
    border-radius: 5px;
    transition: background-color 0.3s ease-in-out;
}

.product a:hover {
    background-color: #0056b3;
}

/* Estilo para el enlace "Home" */
.button-link {
    display: inline-block;
    /* Hace que el enlace se comporte como un bloque */
    padding: 10px 20px;
    /* Agrega espacio alrededor del enlace */
    background-color: #007bff;
    /* Color de fondo azul */
    color: #fff;
    /* Color del texto blanco */
    text-decoration: none;
    /* Quita el subrayado por defecto */
    transition: background-color 0.3s, color 0.3s;
    /* Animación de cambio de color y fondo */
    cursor: pointer;
    /* Cambia el cursor a una mano */
    border-radius: 10px;
    /* Bordes redondeados */
    border: 2px solid #007bff;
    /* Borde azul */
    margin-right: 10px;
}

/* Estilo para el enlace "Home" al pasar el cursor por encima */
.home-link:hover {
    background-color: #0056b3;
    /* Cambia el color de fondo al pasar el cursor por encima */
    color: #fff;
    /* Cambia el color del texto al pasar el cursor por encima */
    border: 2px solid #0056b3;
    /* Cambia el color del borde al pasar el cursor por encima */
}